import React, { useEffect, useState } from 'react';
import { Button, Input } from 'reactstrap';

export const Food = ({item,
  fat,
  editable,
  handleProtOnChange,
}) => {
  const [_id, setId] = useState(item.id);
  const [_units, setUnits] = useState(item.units);
  const [_name, setName] = useState(item.name);
  const [_group, setGroup] = useState(item.group);
  const [_portion, setPortion] = useState(item.portion);
  const [_prot, setProt] = useState(item.prot);
  const [_carb, setCarb] = useState(item.carb);
  const [_fat, setFat] = useState(item.fat);
  const [cals, setCals] = useState();
  const [isEditable, setIsEditable] = useState(editable);
  const [isDone, setIsDone] = useState(false);

  useEffect(() => {
    setIsEditable(editable);
    calulateCalories();
  }, [editable]);

  const calulateCalories = () => {
    setCals(Math.round((_carb + _prot) * 4 + _fat * 9));
  };

  const handleOnclickEdit = e => {
    setIsEditable(true);
  };

  const handleOnclickDone = e => {
    setIsEditable(false);
  };

  const _handleProtOnChange = value => {
    setProt(Number(value));
    calulateCalories();
  };

  const handleCarbOnChange = value => {
    setCarb(Number(value));
    calulateCalories();
  };

  const handleFatOnChange = value => {
    setFat(Number(value));
    calulateCalories();
  };

  return (
    <tr>
      <th scope="row">{_id}</th>
      <td>
        {isEditable ? (
          <Input type="text" value={_name} onChange={event => setName(event.target.value)} />
        ) : (
          _name
        )}
      </td>
      <td>
        {isEditable ? (
          <Input type="text" value={_group} onChange={event => setGroup(event.target.value)} />
        ) : (
          _group
        )}
      </td>
      <td>
        {isEditable ? (
          <Input
            type="number"
            value={_portion}
            onChange={event => setPortion(event.target.value)}
          />
        ) : (
          _portion + ' ' + _units
        )}
      </td>
      <td>{cals}</td>
      <td>
        {isEditable ? (
          <Input
            type="number"
            value={_prot}
            onChange={event => handleProtOnChange(event.target.value)}
          />
        ) : (
          _prot + ' g'
        )}
      </td>
      <td>
        {isEditable ? (
          <Input
            type="number"
            value={_carb}
            onChange={event => handleCarbOnChange(event.target.value)}
          />
        ) : (
          _carb + ' g'
        )}
      </td>
      <td>
        {isEditable ? (
          <Input
            type="number"
            value={_fat}
            onChange={event => handleFatOnChange(event.target.value)}
          />
        ) : (
          _fat + ' g'
        )}
      </td>
      <td>
        <Button
          color={!isEditable ? 'primary' : 'danger'}
          onClick={!isEditable ? handleOnclickEdit : handleOnclickDone}
        >
          {!isEditable ? 'edit' : 'done'}
        </Button>
      </td>
    </tr>
  );
};
